import React from 'react';
import _ from 'lodash';
import { FormattedMessage } from 'react-intl';
import LeftNavBarActions from 'ui-library/lib/components/panels/left-nav/Actions';
import endpoints from '../../utils/apiEndpoints';
import * as types from './actionTypes';
import {
  dispatchSuccessMessage,
  dispatchErrorMessage,
  generateHttpErrorMessage,
  generateAttributeErrors,
  logErrorToConsole,
} from '../../utils/helpers';
import * as routes from '../../utils/routes';
import axios from '../../utils/axiosInstance';
import { RESOURCE_TYPES } from '../../utils/resourceTypes';

export function getUserAction(id, type) {
  return async (dispatch) => {
    try {
      const endpoint = endpoints.endpointForResource(type, { id });
      const res = await axios.get(endpoint);

      dispatch({
        type: types.GET_USER,
        data: {
          user: res.data.data,
          included: res.data.included,
          meta: res.data.meta,
        },
      });
    } catch (error) {
      logErrorToConsole(error);
      const errorMessage = generateHttpErrorMessage(error);
      const { intlKey, statusCode } = errorMessage;

      dispatch({
        type: types.GET_USER_ERROR,
        data: {
          error: intlKey,
        },
      });

      dispatchErrorMessage(
        dispatch,
        <FormattedMessage
          id={intlKey}
          values={{
            statusCode,
          }}
        />,
      );
    }
  };
}

export function createUserAction(attributes, selectedParentId, skipMessage = false) {
  return async (dispatch, getState) => {
    try {
      const selected = _.get(getState(), 'resourceTypes.selected');
      const parentResourceType = _.get(selected, 'attributes.parentResourceType');
      const resourceId = _.get(selected, 'id');
      const endpoint = endpoints.endpointForResource(resourceId);
      const relationships = {
        orgs: {
          data: {
            type: parentResourceType,
            id: selectedParentId,
          },
        },
      };
      let payload = {
        attributes,
      };

      if (selectedParentId) {
        payload = {
          ...payload,
          relationships,
        };
      }

      dispatch({
        type: types.CREATE_USER_TRIGGERED,
        data: {
          payload,
        },
      });

      const res = await axios.post(endpoint, {
        data: payload,
      });

      dispatch({
        type: types.CREATE_USER_SUCCESS,
        data: {
          user: res.data.data,
        },
      });

      // Set the left nav bar to not be on "Create User" item
      dispatch(LeftNavBarActions.selectItem(routes.USERS_SEARCH_ROUTE));

      if (!skipMessage) {
        dispatchSuccessMessage(
          dispatch,
          <FormattedMessage
            id="actions.create.success"
            values={{
              resourceType: _.capitalize(RESOURCE_TYPES.USER),
            }}
          />,
        );
      }

      return Promise.resolve(res);
    } catch (error) {
      logErrorToConsole(error);
      const errorMessage = generateHttpErrorMessage(error);
      const { intlKey, statusCode } = errorMessage;
      const attributeErrors = generateAttributeErrors(error);

      dispatch({
        type: types.CREATE_USER_ERROR,
        data: {
          error: intlKey,
          attributeErrors,
        },
      });

      dispatchErrorMessage(
        dispatch,
        <FormattedMessage
          id={intlKey}
          values={{
            statusCode,
          }}
        />,
      );

      return Promise.reject(error);
    }
  };
}

export function editUserAction(
  id,
  updatedAttributes,
  skipMessage = false,
) {
  return async (dispatch, getState) => {
    try {
      let extraInfo;
      const resourceId = _.get(getState(), 'resourceTypes.selected.id');
      const endpoint = endpoints.endpointForResource(resourceId, { id });
      const res = await axios.patch(endpoint, {
        data: {
          attributes: {
            ...updatedAttributes,
          },
        },
      });

      const resData = _.get(res, 'data.data');
      const resId = resData.id;
      const resAttributes = resData.attributes;

      if (_.isUndefined(resAttributes) || _.isUndefined(resId)) throw new Error();

      if (resId !== id) {
        extraInfo = { newId: resId };
      }

      dispatch({
        type: types.USER_UPDATED,
        data: {
          id,
          updatedAttributes: resAttributes,
          ...extraInfo,
        },
      });

      if (!skipMessage) {
        dispatchSuccessMessage(
          dispatch,
          <FormattedMessage
            id="actions.update.success"
            values={{
              resourceType: _.capitalize(RESOURCE_TYPES.USER),
            }}
          />,
        );
      }

      return Promise.resolve(res);
    } catch (error) {
      logErrorToConsole(error);
      const errorMessage = generateHttpErrorMessage(error);
      const { intlKey, statusCode } = errorMessage;
      const attributeErrors = generateAttributeErrors(error);

      dispatch({
        type: types.USER_UPDATED_ERROR,
        data: {
          error: intlKey,
          attributeErrors,
        },
      });

      dispatchErrorMessage(
        dispatch,
        <FormattedMessage
          id={intlKey}
          values={{
            statusCode,
          }}
        />,
      );

      return Promise.reject(error);
    }
  };
}

export function resetUserPasswordAction(id) {
  return async (dispatch, getState) => {
    try {
      const resourceId = _.get(getState(), 'resourceTypes.selected.id');
      const endpoint = endpoints.endpointForResource(resourceId, { id, password: true });
      const result = await axios.patch(endpoint, {});

      dispatch({
        type: types.RESET_PASSWORD,
        data: {
          id: result.data.data.id,
          updatedAttributes: {
            'dadmin-account-locked': [false],
          },
        },
      });

      return result.data.data.attributes.password;
    } catch (error) {
      logErrorToConsole(error);
      const errorMessage = generateHttpErrorMessage(error);
      const { intlKey, statusCode } = errorMessage;

      dispatch({
        type: types.RESET_PASSWORD_ERROR,
        data: {
          error: intlKey,
        },
      });

      dispatchErrorMessage(dispatch, <FormattedMessage
        id={intlKey}
        values={{
          statusCode,
        }}
      />);

      throw new Error(error);
    }
  };
}

export function clearNewUserAction() {
  return async (dispatch) => {
    dispatch({ type: types.CLEAR_NEW_USER });
  };
}
