import React from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';

import CustomAttributeField from '../../components/shared/forms/CustomAttributeField/CustomAttributeField';

export const CustomAttributeContainer = props => (
  <CustomAttributeField
    {...props}
  />
);

CustomAttributeContainer.propTypes = {
  attr: PropTypes.string.isRequired,
  onValueChange: PropTypes.func.isRequired,
  onValidationChange: PropTypes.func.isRequired,
  value: PropTypes.oneOfType([
    PropTypes.array,
    PropTypes.bool,
    PropTypes.number,
    PropTypes.object,
    PropTypes.string,
  ]),
  resourceType: PropTypes.shape({
    id: PropTypes.string.isRequired,
  }).isRequired,
  metaObject: PropTypes.shape({}).isRequired,
  modelForm: PropTypes.shape({
    isDirty: PropTypes.bool.isRequired,
    isValid: PropTypes.bool.isRequired,
    currentAttrs: PropTypes.shape({}).isRequired,
    updatedAttrs: PropTypes.shape({}).isRequired,
    customIsValid: PropTypes.shape({}).isRequired,
  }).isRequired,
};

CustomAttributeContainer.defaultProps = {
  value: undefined,
};

function mapStateToProps(state) {
  return {
    modelForm: state.modelForm,
  };
}

export default connect(mapStateToProps)(CustomAttributeContainer);
