import React, { Component } from 'react';
import PropTypes from 'prop-types';
import _ from 'lodash';
import moment from 'moment';
import { connect } from 'react-redux';
import { injectIntl, intlShape, FormattedMessage } from 'react-intl';
import Spinner from 'ui-library/lib/components/general/Spinner';
import ButtonBar from 'ui-library/lib/components/forms/ButtonBar';
import Section from 'ui-library/lib/components/general/Section';
import InlineMessage, { MessageTypes } from 'ui-library/lib/components/general/InlineMessage';
import { withRouter } from 'react-router-dom';

import EditableField, { FIELD_TYPES } from '../EditableField/EditableField';
import AlertModal from '../../modals/AlertModal/AlertModal';
import {
  blacklistedDisplayAttrs,
  categorizeAttributes,
} from '../../../../utils/helpers';
import { setModelFormAction } from '../../../../store/actions/modelForm/modelFormActions';

import './ModelForm.css';

// TODO: Refactor with Formik https://gitlab.corp.pingidentity.com/PingData/ds-delegator/issues/5
export class ModelForm extends Component {
  constructor(props) {
    super(props);

    this.handleFormSave = this.handleFormSave.bind(this);
    this.handleChange = this.handleChange.bind(this);
    this.handleCustomFormValidation = this.handleCustomFormValidation.bind(this);
    this.onModalSave = this.onModalSave.bind(this);
    this.onModalCancel = this.onModalCancel.bind(this);
    this.resetForm = this.resetForm.bind(this);

    this.state = {
      isLoading: true,
      nextLocation: undefined,
      openModal: false,
    };
  }

  getInitialState() {
    const {
      newModel,
      model,
      meta,
      blacklist,
    } = this.props;
    const initModel = newModel ? undefined : model;
    let currentAttrs = _.get(initModel, 'attributes', {});

    // Initialize boolean attributes to false so users don't have to check and uncheck them to set
    // them to false.
    if (newModel) {
      const booleanAttributes =
        Object
          .entries(meta.attributes)
          .filter(([key, value]) => blacklist.indexOf(key) < 0 && value.dataType === 'boolean')
          .map(attr => attr[0])
          .reduce((result, attr) => ({
            ...result,
            [attr]: [false],
          }), {});

      currentAttrs = booleanAttributes;
    }

    const customIsValid =
      Object
        .entries(meta.attributes)
        .filter(([key, value]) => blacklist.indexOf(key) < 0 && value.customPresentation)
        .map(attr => attr[0])
        .reduce((result, attr) => ({
          ...result,
          [attr]: true,
        }), {});

    return {
      isDirty: false,
      isValid: false,
      currentAttrs,
      updatedAttrs: {},
      customIsValid,
      attributeErrors: {},
    };
  }

  componentDidMount() {
    this.setInitialModelFormState();

    this.unblock = this.props.history.block((nextLocation) => {
      if (this.props.modelForm.isDirty) {
        this.setState({ openModal: true, nextLocation });
      }
      return !this.props.modelForm.isDirty;
    });
  }

  componentDidUpdate(prevProps) {
    // NOTE: We are ok to use setState here because of the conditional
    // https://reactjs.org/docs/react-component.html#componentdidupdate
    /* eslint-disable react/no-did-update-set-state */
    // replace state when we are changing model instances
    const prevModelId = _.get(prevProps, 'model.id', undefined);
    const currentModelId = _.get(this.props, 'model.id', undefined);

    if (prevModelId !== currentModelId) {
      this.props.setModelFormAction({ currentAttrs: this.props.model.attributes });
    }

    if (prevProps.selected.id !== this.props.selected.id) {
      this.setInitialModelFormState();
    }
  }

  componentWillUnmount() {
    this.unblock();
  }

  onModalSave() {
    const { onSave } = this.props;
    const { updatedAttrs } = this.props.modelForm;

    this.setState({ openModal: false });
    onSave(updatedAttrs);
  }

  onModalCancel() {
    this.setState({ openModal: false, nextLocation: undefined });
  }

  async setInitialModelFormState() {
    await this.props.setModelFormAction(this.getInitialState());
    this.setState({ isLoading: false });
  }

  async resetForm() {
    const { nextLocation } = this.state;

    await this.setInitialModelFormState();
    if (nextLocation) {
      this.props.history.replace(nextLocation.pathname);
      this.props.selectItemLeftNavAction(nextLocation.pathname);
    }
  }

  async handleFormSave() {
    const { onSave } = this.props;
    const { updatedAttrs } = this.props.modelForm;

    try {
      await onSave(updatedAttrs);

      this.props.setModelFormAction({ isDirty: false });
    } catch (e) {
      // Errors handled by actions
    }
  }

  handleChange(attr, newValue) {
    const {
      currentAttrs,
      updatedAttrs,
      customIsValid,
      attributeErrors,
    } = this.props.modelForm;
    const { model, meta } = this.props;
    const newCurrentAttrs = {
      ...currentAttrs,
      [attr]: [newValue],
    };
    const originalAttributes = _.get(model, 'attributes', {});
    const isDirty = !_.isMatch(originalAttributes, newCurrentAttrs);
    const requiredAttrs =
      Object
        .entries(meta.attributes)
        .filter(([_key, value]) => value.required)
        .map(([key, _value]) => key);
    const isValid = requiredAttrs.every((reqAttr) => {
      const attribute = meta.attributes[reqAttr];
      const val = newCurrentAttrs[reqAttr];
      if (val) {
        if (attribute.dataType === FIELD_TYPES.STRING) return typeof val[0] === 'string' && val[0].length;
        else if (attribute.dataType === FIELD_TYPES.INTEGER) return Number.isInteger(val[0]);
        else if (attribute.dataType === FIELD_TYPES.BOOLEAN) return typeof val[0] === 'boolean';
        else if (attribute.dataType === FIELD_TYPES.DATETIME) {
          return moment.isMoment(val[0]) && val[0].isValid();
        }
      }

      return false;
    });

    this.props.setModelFormAction({
      isValid,
      isDirty,
      currentAttrs: newCurrentAttrs,
      updatedAttrs: {
        ...updatedAttrs,
        [attr]: [newValue],
      },
      customIsValid,
      attributeErrors,
    });
  }

  displayAttributes() {
    const { intl, meta, blacklist } = this.props;
    const attributes =
      Object
        .entries(meta.attributes)
        .filter(([key, _value]) => blacklist.indexOf(key) < 0);
    const categories = meta.attributeCategories;

    if (categories && categories.length) {
      const categorizedAttrs = categorizeAttributes(categories, attributes, intl);

      return categorizedAttrs.map(category => (
        <div className="attribute-category" key={category.name}>
          <Section
            title={category.name}
            stateless={false}
            expanded
          >
            {
              category.attributes.map(attribute => (
                <div key={attribute.name}>
                  {this.displayEditableField(attribute.name)}
                </div>
              ))
            }
          </Section>
        </div>
      ));
    }

    return attributes
      .map(([key, _value]) => (
        <div key={key}>
          {this.displayEditableField(key)}
        </div>
      ));
  }

  handleCustomFormValidation(attr, newIsValid) {
    const { modelForm } = this.props;
    const { customIsValid } = modelForm;
    const isValid = _.get(customIsValid, `${attr}`, false);

    if (isValid !== newIsValid) {
      const newcustomIsValid = {
        ...customIsValid,
        [attr]: newIsValid,
      };

      this.props.setModelFormAction({
        ...modelForm,
        customIsValid: newcustomIsValid,
      });
    }
  }

  displayEditableField(attr) {
    const {
      meta,
      selected,
      newModel,
    } = this.props;
    const { currentAttrs, attributeErrors } = this.props.modelForm;
    const metaObject = _.get(meta.attributes, `[${attr}]`, {});
    const {
      dataType,
      displayName,
      readOnly,
      required,
      customPresentation,
    } = metaObject;
    // Don't display read-only attributes on create
    if (newModel && readOnly) return null;

    const errorAttr = attributeErrors && attributeErrors[attr];
    const errorMessage = errorAttr && errorAttr.message;
    let value;
    if (currentAttrs[attr]) {
      [value] = currentAttrs[attr];
    }

    return (
      <div className="input-row">
        <EditableField
          attr={attr}
          attributeErrors={attributeErrors}
          errorMessage={errorMessage}
          label={displayName}
          onEdit={this.handleChange}
          onValidationChange={this.handleCustomFormValidation}
          readOnly={readOnly}
          type={dataType}
          required={required}
          customPresentation={customPresentation}
          value={value}
          resourceType={selected}
          metaObject={metaObject}
        />
      </div>
    );
  }

  render() {
    const {
      intl,
      isSaving,
      newModel,
      parentResource,
    } = this.props;
    const { isDirty, isValid, customIsValid } = this.props.modelForm;
    const { openModal, isLoading } = this.state;
    const cancelText = newModel
      ? intl.formatMessage({ id: 'common.clear-form' })
      : intl.formatMessage({ id: 'common.discard-changes' });

    if (parentResource.error) {
      return (
        <InlineMessage type={MessageTypes.ERROR}>
          <FormattedMessage
            id="actions.resource-types.get-parent-failure"
            values={{
              parentResource: parentResource.id,
            }}
          />
        </InlineMessage>
      );
    }

    const allCustomsValid = Object
      .keys(customIsValid)
      .every(key => customIsValid[key]);

    return (
      <Spinner show={isLoading}>
        <div className="ModelForm">
          <AlertModal
            isOpen={openModal}
            modalTitle={intl.formatMessage({ id: 'containers.model-form.alert-modal.title' })}
            modalDescription={intl.formatMessage({ id: 'containers.model-form.alert-modal.message' })}
            onCancel={this.resetForm}
            cancelText={intl.formatMessage({ id: 'common.discard-changes' })}
            onSave={this.onModalSave}
            saveText={intl.formatMessage({ id: 'common.save' })}
            onClose={this.onModalCancel}
            closeText={intl.formatMessage({ id: 'common.cancel' })}
            preventSave={!isValid}
          />

          {this.displayAttributes()}

          <ButtonBar
            flags={['use-portal']}
            visible={isDirty}
            cancelText={cancelText}
            saveText={intl.formatMessage({ id: 'common.save' })}
            saveDisabled={!isValid || !allCustomsValid}
            enableSavingAnimation={isSaving}
            onCancel={this.resetForm}
            onSave={this.handleFormSave}
          />
        </div>
      </Spinner>
    );
  }
}

ModelForm.propTypes = {
  intl: intlShape.isRequired,
  onSave: PropTypes.func.isRequired,
  isSaving: PropTypes.bool.isRequired,
  meta: PropTypes.shape({
    attributes: PropTypes.shape({}).isRequired,
  }),
  model: PropTypes.shape({
    id: PropTypes.string.isRequired,
    type: PropTypes.string.isRequired,
    attributes: PropTypes.shape({}).isRequired,
    included: PropTypes.arrayOf(PropTypes.shape({})),
  }),
  history: PropTypes.shape({
    replace: PropTypes.func.isRequired,
    block: PropTypes.func.isRequired,
  }).isRequired,
  blacklist: PropTypes.arrayOf(PropTypes.string),
  newModel: PropTypes.bool,
  selectItemLeftNavAction: PropTypes.func.isRequired,
  setModelFormAction: PropTypes.func.isRequired,
  modelForm: PropTypes.shape({
    isDirty: PropTypes.bool.isRequired,
    isValid: PropTypes.bool.isRequired,
    currentAttrs: PropTypes.shape({}).isRequired,
    updatedAttrs: PropTypes.shape({}).isRequired,
    customIsValid: PropTypes.shape({}).isRequired,
    attributeErrors: PropTypes.objectOf(
      PropTypes.shape({
        message: PropTypes.string.isRequired,
      }),
    ).isRequired,
  }).isRequired,
  parentResource: PropTypes.shape({
    id: PropTypes.string,
    error: PropTypes.string,
  }),
  selected: PropTypes.shape({
    id: PropTypes.string.isRequired,
  }).isRequired,
};

ModelForm.defaultProps = {
  meta: undefined,
  model: null,
  blacklist: blacklistedDisplayAttrs,
  newModel: false,
  parentResource: {
    id: undefined,
    error: undefined,
  },
};

function mapStateToProps(state) {
  return {
    modelForm: state.modelForm,
  };
}

export default injectIntl(withRouter(connect(
  mapStateToProps,
  {
    setModelFormAction,
  },
)(ModelForm)));
