import React, { Component } from 'react';
import PropTypes from 'prop-types';
import moment from 'moment';
import FormCheckbox from 'ui-library/lib/components/forms/FormCheckbox';
import FormTextField from 'ui-library/lib/components/forms/form-text-field';
import FormIntegerField from 'ui-library/lib/components/forms/form-integer-field';
import Calendar from 'ui-library/lib/components/calendars/Calendar';

import CustomAttributeContainer from '../../../../containers/CustomAttributeContainer/CustomAttributeContainer';

export const FIELD_TYPES = {
  STRING: 'string',
  INTEGER: 'integer',
  BOOLEAN: 'boolean',
  DATETIME: 'dateTime',
};

class EditableField extends Component {
  constructor(props) {
    super(props);

    this.handleStringChange = this.handleStringChange.bind(this);
    this.handleBooleanChange = this.handleBooleanChange.bind(this);
    this.handleDateChange = this.handleDateChange.bind(this);
    this.handleIntegerChange = this.handleIntegerChange.bind(this);
    this.handleValidationChange = this.handleValidationChange.bind(this);
    this.renderField = this.renderField.bind(this);
  }

  handleStringChange(value) {
    const { attr, onEdit } = this.props;
    onEdit(attr, value);
  }

  handleBooleanChange(event) {
    const { attr, onEdit } = this.props;
    onEdit(attr, event.target.checked);
  }

  handleDateChange(date) {
    const { attr, onEdit } = this.props;
    onEdit(attr, moment(date, 'MM-DD-YYYY').toISOString());
  }

  // Need to handle the case of allowing negative numbers following a hyphen
  handleIntegerChange(value) {
    const { attr, onEdit } = this.props;
    const parsedValue = value === '-' ? value : parseInt(value, 10);

    if (!FormIntegerField.isValid(value) && value !== '-') {
      return;
    }

    onEdit(attr, parsedValue);
  }

  handleValidationChange(isValid) {
    const { attr, onValidationChange } = this.props;
    onValidationChange(attr, isValid);
  }

  renderField() {
    const {
      attr,
      attributeErrors,
      errorMessage,
      label,
      readOnly,
      type,
      required,
      customPresentation,
      value,
      resourceType,
      metaObject,
    } = this.props;

    if (customPresentation) {
      return (
        <>
          <CustomAttributeContainer
            attr={attr}
            value={value}
            resourceType={resourceType}
            onValueChange={this.handleStringChange}
            onValidationChange={this.handleValidationChange}
            metaObject={metaObject}
            attributeErrors={attributeErrors}
          />
        </>
      );
    } else if (type === FIELD_TYPES.STRING || (readOnly && type === FIELD_TYPES.DATETIME)) {
      return (
        <div className="TextField">
          <FormTextField
            flags={['p-stateful']}
            labelText={label}
            value={value || ''}
            onValueChange={this.handleStringChange}
            readOnly={readOnly}
            errorMessage={errorMessage}
            required={required}
            flexWidth
          />
        </div>
      );
    } else if (type === FIELD_TYPES.INTEGER) {
      return (
        <div className="IntegerField">
          <FormIntegerField
            labelText={label}
            initialValue={value}
            value={value}
            onValueChange={this.handleIntegerChange}
            readOnly={readOnly}
            errorMessage={errorMessage}
            required={required}
            min={-999999999999999}
          />
        </div>
      );
    } else if (type === FIELD_TYPES.BOOLEAN) {
      return (
        <div className="Checkbox">
          <FormCheckbox
            className="inline"
            label={label}
            checked={value}
            onChange={this.handleBooleanChange}
            disabled={readOnly}
            errorMessage={errorMessage}
          />
        </div>
      );
    } else if (type === FIELD_TYPES.DATETIME) {
      return (
        <div className="DateTime">
          <Calendar
            flags={['p-stateful', 'use-portal']}
            closeOnSelect
            date={value}
            required={required}
            label={label}
            onValueChange={this.handleDateChange}
            errorMessage={errorMessage}
          />
        </div>
      );
    }

    return null;
  }

  render() {
    const field = this.renderField();

    if (!field) return null;

    return (
      <div className="EditableField">
        {this.renderField()}
      </div>
    );
  }
}

EditableField.propTypes = {
  attr: PropTypes.string.isRequired,
  attributeErrors: PropTypes.objectOf(
    PropTypes.shape({
      message: PropTypes.string.isRequired,
    }),
  ),
  errorMessage: PropTypes.string,
  label: PropTypes.string.isRequired,
  onEdit: PropTypes.func.isRequired,
  onValidationChange: PropTypes.func.isRequired,
  readOnly: PropTypes.bool.isRequired,
  type: PropTypes.string.isRequired,
  required: PropTypes.bool,
  customPresentation: PropTypes.bool.isRequired,
  value: PropTypes.oneOfType([
    PropTypes.array,
    PropTypes.bool,
    PropTypes.number,
    PropTypes.object,
    PropTypes.string,
  ]),
  resourceType: PropTypes.shape({
    id: PropTypes.string.isRequired,
  }).isRequired,
  metaObject: PropTypes.shape({}).isRequired,
};

EditableField.defaultProps = {
  required: false,
  value: undefined,
  errorMessage: '',
  attributeErrors: undefined,
};

export default EditableField;
